`, "",{"version":3,"sources":["webpack://./apps/settings/src/components/AppList/AppScore.vue"],"names":[],"mappings":";AA+DA;CACA,oBAAA;CACA,qCAAA;AAEA;EACA,2BAAA;AACA;AACA","sourcesContent":["<!--\n  - SPDX-FileCopyrightText: 2018 Nextcloud GmbH and Nextcloud contributors\n  - SPDX-License-Identifier: AGPL-3.0-or-later\n-->\n\n<template>\n\t<span role=\"img\"\n\t\t:aria-label=\"title\"\n\t\t:title=\"title\"\n\t\tclass=\"app-score__wrapper\">\n\t\t<NcIconSvgWrapper v-for=\"index in fullStars\"\n\t\t\t:key=\"`full-star-${index}`\"\n\t\t\t:path=\"mdiStar\"\n\t\t\tinline />\n\t\t<NcIconSvgWrapper v-if=\"hasHalfStar\" :path=\"mdiStarHalfFull\" inline />\n\t\t<NcIconSvgWrapper v-for=\"index in emptyStars\"\n\t\t\t:key=\"`empty-star-${index}`\"\n\t\t\t:path=\"mdiStarOutline\"\n\t\t\tinline />\n\t</span>\n</template>\n<script lang=\"ts\">\nimport NcIconSvgWrapper from '@nextcloud/vue/components/NcIconSvgWrapper'\nimport { mdiStar, mdiStarHalfFull, mdiStarOutline } from '@mdi/js'\nimport { translate as t } from '@nextcloud/l10n'\nimport { defineComponent } from 'vue'\n\nexport default defineComponent({\n\tname: 'AppScore',\n\tcomponents: {\n\t\tNcIconSvgWrapper,\n\t},\n\tprops: {\n\t\tscore: {\n\t\t\ttype: Number,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tsetup() {\n\t\treturn {\n\t\t\tmdiStar,\n\t\t\tmdiStarHalfFull,\n\t\t\tmdiStarOutline,\n\t\t}\n\t},\n\tcomputed: {\n\t\ttitle() {\n\t\t\tconst appScore = (this.score * 5).toFixed(1)\n\t\t\treturn t('settings', 'Community rating: {score}/5', { score: appScore })\n\t\t},\n\t\tfullStars() {\n\t\t\treturn Math.floor(this.score * 5 + 0.25)\n\t\t},\n\t\temptyStars() {\n\t\t\treturn Math.min(Math.floor((1 - this.score) * 5 + 0.25), 5 - this.fullStars)\n\t\t},\n\t\thasHalfStar() {\n\t\t\treturn (this.fullStars + this.emptyStars) < 5\n\t\t},\n\t},\n})\n</script>\n<style scoped>\n.app-score__wrapper {\n\tdisplay: inline-flex;\n\tcolor: var(--color-favorite, #a08b00);\n\n\t> * {\n\t\tvertical-align: text-bottom;\n\t}\n}\n</style>\n"],"